import React, { PureComponent } from 'react';
import { NavBar, Icon, } from 'antd-mobile';
import { connect } from 'react-redux';
import Content from '../../views/common/Content';

import '../../assets/css/traffic.css';

class TrafficTrainScreen extends PureComponent
{
    render()
    {
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            icon={<Icon type="left" />}
                            onLeftClick={() => this.props.history.goBack()}>
                            火车
                        </NavBar>
                }
                <Content style={{backgroundColor: '#efeff4'}}>
                    <div className="trafficDetailCard">
                        <img src={require('../../assets/images/traffic/train-1.png')} alt='' className="trafficDetailImage"/>
                        
                        <div className="trafficDetailNameRow">
                            <div className="trafficDetailNames">
                                <div className="trafficDetailName">
                                    禹州站
                                </div>
                                <div className="trafficDetailSubName">
                                    Yuzhou East Railway Station
                                </div>
                            </div>
                            <div className="trafficDetailNameFlag">
                                三等站
                            </div>
                        </div>

                        <div className="trafficDetailLR">
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">地址</div>
                                <div className="trafficDetailLRSubItem">河南省许昌市禹州市</div>
                            </div>
                            <div className="trafficDetailSplitLR"/>
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">始建时间</div>
                                <div className="trafficDetailLRSubItem">建设中</div>
                            </div>
                        </div>

                        <div className="trafficDetailIntro">
                            <div className="trafficDetailIntroLine"/>
                            <div className="trafficDetailIntroText">火车站简介</div>
                            <div className="trafficDetailIntroLine"/>
                        </div>

                        <div className="trafficDetailContent">
                            <div className="trafficDetailLongText">
                                禹州火车站，即郑万高铁禹州东站。郑万高铁项目禹州段涉及褚河、范坡、古城、郭连、山货、小吕和张得等7个乡镇，拟占用土地83.9819公顷。从长远规划看，高铁正北越过郑州直达北京，正南穿过重庆远至昆明。高铁建成后，不但能吸引八方来客，引进外来投资，振兴禹州经济，促进禹州全面发展，城市生活水平大提高。而且我们禹州人出门经商、工作、上学、旅游、探亲、访友也方便多了，拉近了禹州与全国各大城市和旅游热点地区的时空距离。高铁的建成给禹州发展带来了历史机遇，古老的禹州将借助高铁这条天路腾飞起来。禹州市抓住郑万高铁在禹州设站的机遇，认真做好站址周边区域发展规划，谋划“零距离换乘中心”综合交通设施项目，逐步实现区域交通、物流、旅游一体化发展。
                            </div>
                        </div>

                        <div className="trafficDetailContent">
                            <div className="trafficDetailTitle1">概述</div>
                            <div className="trafficDetailLongText">
                                2014年9月10日，国家发改委以发改基础（2014）2043号文，正式批复新建郑州至万州铁路项目建议书.
                            </div>
                            <div className="trafficDetailLongText">
                                2015年6月8日，记者从省发改委获悉，目前郑万高铁可行性研究报告已通过专家评审，设计速度也由原来的250公里/时提升到350公里/时。
                            </div>
                            <div className="trafficDetailLongText">
                                据悉，日前国家发改委委托中国国际工程咨询公司在重庆审查了郑万铁路可行性研究报告。根据计划，郑万铁路将于今年年底前全面开工。郑万铁路也成为国务院明确要求今年开工的60个重大铁路项目之一。
                            </div>
                            <div className="trafficDetailLongText">
                                此次郑万铁路可行性研究报告评估会议还明确了郑万铁路线路走向，确定新建郑万铁路起于郑州东站，途经我省开封市（尉氏）、许昌市、禹州市、平顶山市、南阳市、邓州市后进入湖北省境内，经襄阳市襄城区、南漳县、保康县、神农架林区、兴山县、恩施州巴东县后进入重庆市境内，经巫山县、奉节县、云阳县至万州区接上在建的渝万客专。线路全长818.094公里,估算总投资1195亿元。
                            </div>
                            <div className="trafficDetailLongText">
                                根据计划，郑万高铁将于7月30日前完成可行性研究报告审批，10月底前开工重大节点工程，年底前全面开工。此外，记者了解到，报告明确郑万铁路定位为客运专线，设计行车速度为350公里/时。按此计算，郑万高铁建成通车后，重庆到郑州车程时间将缩短至4小时以内。
                            </div>
                            <div className="trafficDetailLongText">
                                目前禹州境内高铁线路、走向及站位的方案正在优化中。高速铁路将对禹州市交通、旅游、对外开放、拉动产业升级等方面产生深远影响，并为禹州市经济社会发展注入新的的动力，带来前所未有的发展机遇和挑战。
                            </div>
                        </div>
                    </div>
                </Content>
            </div>
        );
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
        isFromNativeApp: state.native.isFromNativeApp
    };
};

TrafficTrainScreen = connect(mapStateToProps)(TrafficTrainScreen);

export default TrafficTrainScreen;


